article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
main,
nav,
section,
summary {
  display: block;
}
audio,
canvas,
video {
  display: inline-block;
}
audio:not([controls]) {
  display: none;
  height: 0;
}
[hidden] {
  display: none;
}
html {
  font-family: sans-serif;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}
body {
  margin: 0;
}
a:focus {
  outline: thin dotted;
}
a:active,
a:hover {
  outline: 0;
}
h1 {
  font-size: 2em;
  margin: 0.67em 0;
}
abbr[title] {
  border-bottom: 1px dotted;
}
b,
strong {
  font-weight: bold;
}
dfn {
  font-style: italic;
}
hr {
  -moz-box-sizing: content-box;
  box-sizing: content-box;
  height: 0;
}
mark {
  background: #ff0;
  color: #000;
}
code,
kbd,
pre,
samp {
  font-family: monospace, serif;
  font-size: 1em;
}
pre {
  white-space: pre-wrap;
}
q {
  quotes: "\201C" "\201D" "\2018" "\2019";
}
small {
  font-size: 80%;
}
sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}
sup {
  top: -0.5em;
}
sub {
  bottom: -0.25em;
}
img {
  border: 0;
}
svg:not(:root) {
  overflow: hidden;
}
figure {
  margin: 0;
}
fieldset {
  border: 1px solid #c0c0c0;
  margin: 0 2px;
  padding: 0.35em 0.625em 0.75em;
}
legend {
  border: 0;
  padding: 0;
}
button,
input,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
  margin: 0;
}
button,
input {
  line-height: normal;
}
button,
select {
  text-transform: none;
}
button,
html input[type="button"],
input[type="reset"],
input[type="submit"] {
  -webkit-appearance: button;
  cursor: pointer;
}
button[disabled],
html input[disabled] {
  cursor: default;
}
input[type="checkbox"],
input[type="radio"] {
  box-sizing: border-box;
  padding: 0;
}
input[type="search"] {
  -webkit-appearance: textfield;
  -moz-box-sizing: content-box;
  -webkit-box-sizing: content-box;
  box-sizing: content-box;
}
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}
button::-moz-focus-inner,
input::-moz-focus-inner {
  border: 0;
  padding: 0;
}
textarea {
  overflow: auto;
  vertical-align: top;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
* {
  margin: 0;
  padding: 0;
  text-shadow: 0 0 1px rgba(0, 0, 0, 0.1);
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  -webkit-font-smoothing: subpixel-antialiased;
  -moz-osx-font-smoothing: grayscale;
}
*::before,
*::after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
h3,
h4 {
  margin: 1.5em 0 .5em;
}
h3 {
  font-size: 1.5em;
  line-height: 1.3;
}
.cb {
  -moz-box-sizing: content-box;
  -webkit-box-sizing: content-box;
}
.fading {
  opacity: 0.1 !important;
}
::selection {
  background: #986794;
  color: #fff;
}
::-moz-selection {
  background: #986794;
  color: #fff;
}
body {
  background: none repeat scroll 0 0 #eee;
  color: #333;
  font-family: 'Roboto', 'Helvetica Neue', Helvetica, 'Hiragino Sans GB', 'Hiragino Sans GB W3', 'Microsoft YaHei', sans-serif;
  font-size: 12px;
  position: relative;
  margin: 0 auto;
}
body.header-fixed #menu-mobile {
  top: 0;
}
body.header-fixed #header {
  position: fixed;
  top: -48px;
  left: 0;
  width: 100%;
  z-index: 999;
}
a {
  color: #333;
  cursor: pointer;
  text-decoration: none;
}
blockquote {
  background-color: #f3e077;
  position: relative;
  color: #484019;
  padding: 40px 25px 30px;
  margin-bottom: 20px;
  margin-left: -25px;
  margin-right: -25px;
}
blockquote p {
  display: inline;
}
blockquote::before {
  position: absolute;
  content: 'Note :';
  left: 0;
  top: 0;
  padding: 3px 10px;
  color: #fafafa;
  font-size: 15px;
  background: rgba(0, 0, 0, 0.2);
  -webkit-border-radius: 1px 0 1px 0;
  -moz-border-radius: 1px 0 1px 0;
  border-radius: 1px 0 1px 0;
}
pre {
  white-space: pre-wrap;
  white-space: -moz-pre-wrap;
  white-space: -pre-wrap;
  white-space: -o-pre-wrap;
  word-wrap: break-word;
}
[class^="icon-"] {
  transition: all 0.3s ease-in-out;
}
.clearfix:before,
.clearfix:after {
  content: " ";
  display: table;
}
.clearfix:after {
  clear: both;
}
.display-none {
  display: none;
}
.left {
  float: left;
}
.right {
  float: right;
}
.icon-twitter:hover {
  color: #00a0d1;
}
.icon-weibo:hover {
  color: #eb182c;
}
.icon-flickr:hover {
  color: #ff0084;
}
.icon-googleplus:hover {
  color: #db4a39;
}
.icon-instagram:hover {
  color: #634d40;
}
.icon-foursquare:hover {
  color: #25a0ca;
}
button,
html input[type="button"],
input[type="reset"],
input[type="submit"] {
  -webkit-appearance: button;
  cursor: pointer;
}
.c-ripple {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  background: transparent;
}
.pull-left {
  float: left;
}
.pull-right {
  float: right;
}
.c-ripple__circle {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  opacity: 0;
  width: 0;
  height: 0;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.25);
}
.c-ripple.is-active .c-ripple__circle {
  animation: a-ripple 0.4s ease-in;
}
.overlay-content {
  opacity: 0;
  visibility: hidden;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.75);
  z-index: 100;
  transition: all 0.3s ease-in-out;
}
.overlay-content.show {
  opacity: 1;
  visibility: visible;
}
