.icon {
  position: relative;
  display: inline-block;
  width: auto;
  height: 14px;
  fill: #202121;
  transform: translate(0, 0);
  transition: all 0.3s ease-in-out;
}
.ribbon {
  font-size: 16px;
  width: 80%;
  position: relative;
  background: #ba89b6;
  color: #fff;
  text-align: center;
  padding: 1em 2em;
  margin: 2em auto 3em;
}
.ribbon ::selection {
  color: #986794;
  background: #fff;
}
.ribbon ::-moz-selection {
  color: #986794;
  background: #fff;
}
.ribbon .ribbon-content::before {
  content: "";
  position: absolute;
  display: block;
  border-style: solid;
  border-color: #804f7c transparent transparent transparent;
  bottom: -1em;
  left: 0;
  border-width: 1em 0 0 1em;
}
.ribbon .ribbon-content::after {
  content: "";
  position: absolute;
  display: block;
  border-style: solid;
  border-color: #804f7c transparent transparent transparent;
  bottom: -1em;
  right: 0;
  border-width: 1em 1em 0 0;
}
.ribbon::before {
  content: "";
  position: absolute;
  display: block;
  bottom: -1em;
  border: 1.5em solid #986794;
  z-index: -1;
  left: -2em;
  border-right-width: 1.5em;
  border-left-color: transparent;
}
.ribbon::after {
  content: "";
  position: absolute;
  display: block;
  bottom: -1em;
  border: 1.5em solid #986794;
  z-index: -1;
  right: -2em;
  border-left-width: 1.5em;
  border-right-color: transparent;
}
@media only screen and (max-width: 767px) {
  .ribbon {
    background: #fff;
    color: #333;
    font-size: 25px;
    margin: 0;
    width: 100%;
    padding: 0;
  }
  .ribbon strong {
    font-weight: normal;
  }
  .ribbon::before,
  .ribbon::after,
  .ribbon .ribbon-content::before,
  .ribbon .ribbon-content::after {
    display: none;
  }
}
input:focus,
select:focus,
textarea:focus,
button:focus {
  outline: none;
}
.rika-textfield {
  position: relative;
  font-size: 16px;
  width: 100%;
  margin: 0;
  padding: 20px 0;
}
.rika-textfield input,
.rika-textfield textarea {
  border: none;
  border-bottom: 1px solid rgba(0, 0, 0, 0.12);
  display: inline-block;
  font-size: 16px;
  margin: 0;
  padding: 4px 0;
  width: 100%;
  background: 16px;
  text-align: left;
  color: inherit;
}
.rika-textfield input.is-filled + label,
.rika-textfield textarea.is-filled + label {
  opacity: 0;
  visibility: hidden;
}
.rika-textfield input:focus,
.rika-textfield input:active,
.rika-textfield textarea:focus,
.rika-textfield textarea:active {
  outline: none;
}
.rika-textfield input:focus + label.comment-label,
.rika-textfield input:active + label.comment-label,
.rika-textfield textarea:focus + label.comment-label,
.rika-textfield textarea:active + label.comment-label {
  top: 4px;
  font-size: 12px;
  color: #986794;
  visibility: visible;
  opacity: 1;
}
.rika-textfield input:focus + label.comment-label:after,
.rika-textfield input:active + label.comment-label:after,
.rika-textfield textarea:focus + label.comment-label:after,
.rika-textfield textarea:active + label.comment-label:after {
  visibility: visible;
  left: 0;
  width: 100%;
}
.rika-textfield label.comment-label {
  bottom: 0;
  color: rgba(0, 0, 0, 0.26);
  font-size: 15px;
  left: 0;
  right: 0;
  pointer-events: none;
  position: absolute;
  top: 24px;
  width: 100%;
  overflow: hidden;
  white-space: nowrap;
  text-align: left;
  transition: top 0.2s ease-in-out;
}
.rika-textfield label.comment-label::after {
  background-color: #986794;
  bottom: 20px;
  content: '';
  height: 2px;
  left: 45%;
  position: absolute;
  visibility: hidden;
  width: 10px;
  transition: all 0.2s ease-in-out;
}
.rika-textfield textarea {
  height: 50px;
  transition: height 0.2s ease-in-out;
}
.rika-textfield textarea::-webkit-input-placeholder {
  font-size: 15px;
  color: rgba(0, 0, 0, 0.26);
}
.rika-textfield textarea::-moz-placeholder {
  font-size: 15px;
  color: rgba(0, 0, 0, 0.26);
}
.rika-textfield textarea:-ms-input-placeholder {
  font-size: 15px;
  color: rgba(0, 0, 0, 0.26);
}
.rika-textfield textarea:focus,
.rika-textfield textarea:active {
  height: 150px;
}
button {
  -webkit-appearance: none;
  position: relative;
  display: inline-block;
  padding: 12px 24px;
  margin: 0;
  vertical-align: middle;
  overflow: visible;
  color: #fff;
  font: inherit;
  font-size: 14px;
  line-height: 20px;
  font-weight: 400;
  text-align: center;
  letter-spacing: 1px;
  background: transparent;
  border: 0;
  user-select: none;
  width: 100%;
  transition: all 0.2s ease;
}
button:hover,
button:focus {
  outline: 0;
  text-decoration: none;
}
.rika-button--primary {
  background: #986794;
  text-shadow: 2px 2px 0 #7a5276;
}
.rika-button--primary:hover {
  background: #895d85;
}
.bt-close::before,
.bt-close::after {
  content: '';
  position: absolute;
  top: 33px;
  right: 27px;
  width: 20px;
  height: 3px;
  background: #202121;
  transform: rotate(45deg);
  transition: all 0.3s ease-in-out;
}
.bt-close::after {
  transform: rotate(-45deg);
}
.bt-close:hover {
  opacity: .75;
}
@keyframes moveRight {
  to {
    -webkit-transform: translateX(40px);
    -moz-transform: translateX(40px);
    -ms-transform: translateX(40px);
    transform: translateX(40px);
  }
}
@keyframes moveLeft {
  to {
    -webkit-transform: translateX(-40px);
    -moz-transform: translateX(-40px);
    -ms-transform: translateX(-40px);
    transform: translateX(-40px);
  }
}
@keyframes heartbeat {
  0% {
    -webkit-transform: scale(0.9);
    -ms-transform: scale(0.9);
    -o-transform: scale(0.9);
    transform: scale(0.9);
  }
  9% {
    -webkit-transform: scale(1.2);
    -ms-transform: scale(1.2);
    -o-transform: scale(1.2);
    transform: scale(1.2);
  }
  18% {
    -webkit-transform: scale(0.9);
    -ms-transform: scale(0.9);
    -o-transform: scale(0.9);
    transform: scale(0.9);
  }
  25% {
    -webkit-transform: scale(1);
    -ms-transform: scale(1);
    -o-transform: scale(1);
    transform: scale(1);
  }
  100% {
    -webkit-transform: scale(1);
    -ms-transform: scale(1);
    -o-transform: scale(1);
    transform: scale(1);
  }
}
@keyframes a-ripple {
  0% {
    opacity: 0;
  }
  25% {
    opacity: 1;
  }
  100% {
    width: 200%;
    padding-bottom: 200%;
    opacity: 0;
  }
}
.header {
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 70px;
  line-height: 70px;
  font-size: 13px;
  font-weight: 400;
  border-bottom: 1px solid #e6eaea;
  background-color: #f4f7f6;
  z-index: 302;
  transition: all 0.3s ease-in-out;
}
.header__logo {
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  width: auto;
  height: 26px;
  line-height: normal;
  transform: translate(-50%, -50%);
}
.header__logo a {
  display: inline-block;
  font-family: 'VT323', monospace;
  letter-spacing: 9px;
  color: #a09f9f;
  font-size: 26px;
}
.header__logo span {
  color: black;
}
.header__logo h1 {
  font-size: 26px;
  font-weight: 600;
  margin: 0;
}
.header__item {
  padding: 0 23px;
}
.header__item--search {
  cursor: pointer;
  flex: 1;
}
.header__item--search svg {
  width: 14px;
  height: 14px;
  position: absolute;
  top: 50%;
  right: 23px;
  transform: translateY(-50%);
}
@media (min-width: 768px) {
  .header__item--search svg {
    position: relative;
    top: -1px;
    right: inherit;
    vertical-align: middle;
    transform: translateY(0);
  }
}
.header__item--search .text {
  visibility: hidden;
  opacity: 0;
  margin-left: 8px;
  transition: all 0.3s ease-in-out;
}
.header--fixed .header__item--search .text {
  visibility: visible;
  opacity: 1;
}
.header__item--login {
  position: relative;
  width: 190px;
  height: 70px;
  padding: 0;
  overflow: hidden;
  transition: width 0.3s ease-in-out;
  display: none;
}
.header.header--fixed .header__item--login {
  width: 66px;
}
@media (min-width: 768px) {
  .header__item--login {
    display: block;
  }
}
.header__item--login a {
  position: relative;
  display: inline-block;
  background-color: #986794;
  color: white;
  text-align: center;
  overflow: hidden;
  width: 190px;
  height: 70px;
  line-height: 70px;
  transition: all 0.3s ease-in-out;
}
.header__item--login a:hover {
  background-color: #7a5276;
}
.header__item--login a span {
  font-family: 'Clicker Script', cursive;
  font-size: 2em;
  font-weight: 500;
  position: relative;
  z-index: 1;
}
.header__item--login__wrapper {
  opacity: 0;
  visibility: hidden;
  position: absolute;
  top: 0;
  left: 0;
  float: left;
  width: 100%;
  height: 70px;
  background: #986794;
  text-align: center;
  cursor: pointer;
  z-index: 2;
  transition: all 0.3s ease-in-out;
}
.header.header--fixed .header__item--login__wrapper {
  opacity: 1;
  visibility: visible;
}
.header.header--fixed .header__item--login__wrapper svg {
  top: -1px;
  height: 17px;
  width: 17px;
  fill: #fff;
  vertical-align: middle;
}
.header__menu {
  float: left;
  width: 65px;
  border-right: 1px solid #e6eaea;
  color: #202121;
  cursor: pointer;
  overflow: hidden;
  transition: all 0.3s ease-in-out;
}
@media (min-width: 768px) {
  .header__menu {
    min-width: 115px;
    height: 70px;
  }
}
.header__menu:hover {
  opacity: .75;
}
.header__menu .icon {
  top: -1px;
  vertical-align: middle;
  margin-right: 10px;
  height: 13px;
  width: 15px;
}
.header__menu > span {
  text-transform: uppercase;
  display: none;
}
@media (min-width: 768px) {
  .header__menu > span {
    display: inline-block;
